'use strict';

document.addEventListener("DOMContentLoaded", function(event) {
    const ProjectsPage = {
        
        setData: function() {
            function get(url) {
                return new Promise(function(resolve, reject) {
                    let req = new XMLHttpRequest();
                    req.open('GET', url);
                    req.onload = function() {
                        if (req.status == 200) {
                            resolve(req.response);
                        } else {
                            reject(Error(req.statusText));
                        }
                    }

                    req.onerror = function() {
                        reject(Error('Network Error'));
                    }

                    req.send();
                });
            }

            function getJSON(url) {
                return get(url).then(JSON.parse);
            }

            // Set tags
            getJSON('https://apiprod.denisglazkov.best/tag/list').then(function(data) {
                let tags = {};

                for (let i = 0; i < data.data.length; i++) {
                    tags[i] = {
                        id: data.data[i].id,
                        title: data.data[i].attributes.title,
                        priority: data.data[i].attributes.priority
                    }
                }

                return tags;
            }).then(function(tags) {
                let filter = document.querySelector('.projects__filter');
                let sortTags = [];

                for (let prop in tags) {
                    sortTags.push([tags[prop].priority, tags[prop]]);
                }

                sortTags.sort(function(a, b) {
                    return a[0] - b[0];
                });

                for (let i = 0; i < sortTags.length; i++) {
                    let li = document.createElement('li');
                    let span = document.createElement('span');
                    let a = document.createElement('a');
    
                    span.classList.add('projects__black-border');
                    a.href = `/tag/${sortTags[i][1].id}`;
                    a.innerText = sortTags[i][1].title;
                    li.appendChild(span);
                    li.appendChild(a);

                    filter.appendChild(li);
                }
            });

            // Set Projects
            getJSON('https://apiprod.denisglazkov.best/project/list').then(function(data) {
                let project = {};

                for (let i = 0; i < data.data.length; i++) {
                    if (data.data[i].attributes.is_published) {

                        project[i] = {
                            id: data.data[i].id,
                            title: data.data[i].attributes.title,
                            location: data.data[i].attributes.location,
                            imgID: data.data[i].relationships[0].id,
                            priority: data.data[i].attributes.priority
                        };
                    }
                }

                return project;

            }).then(function(data) {
                let sortProjects = [];

                function getImages() {
                    for (let prop in data) {
                        sortProjects.push([data[prop].priority, data[prop]]);
                    }

                    sortProjects.sort(function(a, b) {
                        return a[0] - b[0];
                    });

                    return new Promise((resolve, reject) => {
                        let imgURI = [];
                        let projectsCount = Object.keys(data).length;

                        for (let prop in data) {
                            getJSON(`https://apiprod.denisglazkov.best/image/${data[prop].imgID}`).then(function(img) {
                                data[prop].imgURI = img.data.attributes.uri;
                                imgURI.push(img.data.attributes.uri);
                            });
                        }

                        let timerID = setInterval(function() {
                            if (imgURI.length == projectsCount) {
                                resolve();
                                clearInterval(timerID);
                            }
                        }, 100);
                    });
                }

                getImages().then(function() {
                    for (let i = 0; i < sortProjects.length; i++) {

                        let list = document.querySelector('.projects__list');
                        let element = `
                        <div class="projects__project">
                            <a class="projects__img" href="/project/${sortProjects[i][1].id}" style="background-image: url('https://apiprod.denisglazkov.best/static${sortProjects[i][1].imgURI}')">
                                <div class="projects__info">
                                    <div class="box">
                                        <div class="draw">
                                            <span class="top"></span>
                                            <span class="right"></span>
                                            <span class="bottom"></span>
                                            <span class="left"></span>
                                            <h3 class="projects__name">${sortProjects[i][1].title}</h3>
                                        </div>
                                    </div>
                                </div>
                                <div class="projects__hidenName">${sortProjects[i][1].title}</div>
                            </a>
                        </div>`;

                        let elementReverse = `
                        <div class="projects__projectReverse">
                            <div class="projects__info">
                                <h3 class="projects__name">${sortProjects[i][1].title}</h3>
                                <a class="btn-transparent detect-inview" href="/project/${sortProjects[i][1].id}">
                                    <span class="btn-line-1"><i></i></span>
                                    <span class="btn-line-2"><i></i></span>
                                    <span class="btn-line-3"><i></i></span>
                                    <span class="btn-line-4"><i></i></span>
                                    <span class="hover-out"><span class="hover-out_i">Show details</span></span>
                                    <span class="hover"><span class="hover_i">Show details</span></span>
                                </a>
                            </div>
                            <a class="projects__img" href="/project/${sortProjects[i][1].id}" style="background-image: url('https://apiprod.denisglazkov.best/static${sortProjects[i][1].imgURI}')">
                                <div class="box">
                                    <div class="draw">
                                        <span class="top"></span>
                                        <span class="right"></span>
                                        <span class="bottom"></span>
                                        <span class="left"></span>
                                    </div>
                                    <div class="projects__hidenName">${sortProjects[i][1].title}</div>
                                </div>
                            </a>
                        </div>`;
                    
                        if (i % 2 === 0) {
                            list.insertAdjacentHTML('beforeend', element);
                        } else {
                            list.insertAdjacentHTML('beforeend', elementReverse);
                        }
                    }

                    let windowHeight = document.documentElement.clientHeight;
                    
                    function showVisible() {
                        let elems = document.querySelectorAll('.btn-transparent');
                        for (let i = 0; i < elems.length; i++) {
                            let elem = elems[i];
                            if (isVisible(elem)) {
                                elem.classList.add('inview');
                            }
                        }
                    }
                    
                    function isVisible(elem) {
                        let coords = elem.getBoundingClientRect();
                        let topVisible = coords.top >= 0 && coords.top < windowHeight;
                        let bottomVisible = coords.bottom < windowHeight && coords.bottom >= 0;
                        return topVisible || bottomVisible;
                    }
                    
                    document.addEventListener('scroll', function() {
                        showVisible();
                    });
                });
            });
        },
        init: function() {
            this.setData();
        }
    }
    
    ProjectsPage.init();
});